.footer {
  justify-content: flex-start;
  background: #f2f2f2;
  width: 100vw;
  align-self: flex-end;
}
.footer__logo {
  margin: 28px 40px 28px 130.03px;
}
.footer__body__information {
  margin-top: 22px;
}
.footer__body__information__line {
  margin-bottom: 6px;
  font-family: Rubik;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #333333;
}
.footer__body__link {
  margin-top: 4px;
  text-decoration: none;
  font-family: Rubik;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #1f4766;
}
